import { PropsWithChildren } from "react"
import { OverridableComponent, OverridableTypeMap } from "@mui/material/OverridableComponent"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Typography } from "@rp/react/web/mui/core"
import { CheckCircle } from "@rp/react/web/mui/icons"

type Props = PropsWithChildren<{
  Icon?: OverridableComponent<OverridableTypeMap>
  iconText?: string
  isCompleted?: boolean
}>

const BriefStepsContent = ({ children, Icon, iconText, isCompleted }: Props) => {
  const { isMobile } = useDeviceInfo()
  const wrapperBoxMl = isMobile ? undefined : `${Icon ? 56 : 0}px`
  return (
    <Box
      sx={{
        backgroundColor: "grey.800",
        p: 2,
        borderRadius: 2,
        mt: 1,
        ml: wrapperBoxMl,
      }}
    >
      <Box
        sx={{
          display: "flex",
          gap: 0.5,
          py: 0.5,
          px: 1,
          borderRadius: 1,
          width: "fit-content",
          "& svg": { width: 20, height: 20 },
        }}
      >
        {isCompleted ? <CheckCircle color="success" /> : Icon && <Icon />}
        {iconText && (
          <Typography sx={{ fontSize: 14, fontWeight: 700, ml: 0.5 }}>{iconText}</Typography>
        )}
      </Box>
      {children}
    </Box>
  )
}

export { BriefStepsContent }
